import React, {Component} from 'react';
import axios from 'axios';
import {Link} from 'react-router-dom';


class AddSensor extends Component{
    constructor(props){
        super(props);
        this.state ={
            proprietaryParameters:[]
        }
        this.url= localStorage.getItem("driverurl");
    }

    componentWillMount(){
        this.getDriverMetadata();
    }

    
    getDriverMetadata(){
        
       axios.get(`${this.url}/api/drivers/metadata`)
        .then(response => {
            this.setState({proprietaryParameters: response.data.sensor_proprietaryParameters}, () =>
        {
            console.log(this.state);
        })
        })
        .catch(err => console.log(err));
    }

    onSubmit(e){
        const pp=this.state.proprietaryParameters;
        let json_pp=[];
        for (let i=0; i<pp.length; i++){
            let ipp={};
            ipp.name = pp[i][0];
            ipp.value = this.refs[pp[i][0]].value;
            json_pp.push(ipp);
        }
        const newSensor ={
            _did:  this.props.match.params.did,
            _sid:  this.refs.sid.value,
            name:  this.refs.name.value,
            processingExpression:  this.refs.processingExpression.value,
            historicData:  true,
            actuator:  true,
            properties:  {
                name: this.refs.propertiesname.value,
                type: this.refs.type.value,
                unit: this.refs.unit.value
            },
            driver: {
                name:"opc_ua_1",
                protocol:"opc_ua",
                version:"1",
                description:"driver for opc_ua"
            },
            events: [
                {
                    type: "sampling",
                    intervalTime: this.refs.samplingInterval.value
                }
            ],
            propietaryParameters: json_pp
        }
        this.addSensor(newSensor);
        e.preventDefault();
    }

    addSensor(newSensor){
        
         axios.request({
             method:'post',
             url:`${this.url}/api/sensors/devices/${newSensor._did}/sensors`,
            data: newSensor
        })         
        .then(response => {
            if (response.status==204){
                this.props.history.push(`/devices/${newSensor._did}`);
            }
        })
        .catch(err => console.log(err));
    }

    render(){
        const proprietaryParameters = this.state.proprietaryParameters.map((parameter, i) => {
            return (
                <div className="input-field">
                        <input type="text" name={parameter[0]} ref={parameter[0]} placeholder={parameter[1]} />
                        <label className="active" htmlFor={parameter[0]}>{parameter[0]}</label>
                        </div>
            )
        })

        return (
            <div>
                <Link className="btn grey" to="/">Back</Link>
                <br />
                
                <h4>Add Sensor</h4>
                <br/>
                <form onSubmit={this.onSubmit.bind(this)}>
                    <div className="input-field">
                        <input type="text" name="sid" ref="sid" placeholder="s1"/>
                        <label className="active" htmlFor="did">Sensor ID</label>
                        </div>
                        <div className="input-field">
                        <input type="text" name="name" ref="name" placeholder="sensor 1"/>
                        <label className="active" htmlFor="name">Name</label>
                        </div>
                        <h6>Properties</h6>
                        <div className="input-field">
                        <input type="text" name="type" ref="propertiesname" placeholder="Distance/Temperature" />
                        <label className="active" htmlFor="propertiesname">What is Measuring</label>
                        </div>
                        <div className="input-field">
                        <input type="text" name="type" ref="type" placeholder="Boolean/Integer/Double/String" />
                        <label className="active" htmlFor="type">Type</label>
                        </div>
                        <div className="input-field">
                        <input type="text" name="unit" ref="unit" placeholder="meters/degrees" />
                        <label className="active" htmlFor="unit">Unit</label>
                        </div>
                        <h6>Event</h6>
                        <div className="input-field">
                        <input type="text" name="samplingInterval" ref="samplingInterval" placeholder="100"/>
                        <label className="active" htmlFor="samplingInterval">Sampling Interval</label>
                        </div>
                        <h6>Computing expression</h6>
                        <div className="input-field">
                        <input type="text" name="processingExpression" ref="processingExpression" value="%v"/>
                        <label className="active" htmlFor="processingExpression">Processing Expression (e.g. doubling a value is: 2*%v)</label>
                        </div>
                        <h6>Proprietary Parameters</h6>
                        {proprietaryParameters}
                    <input type="submit" value="Save" className="btn"/>
                </form>
            </div>
        )
    }
}

export default AddSensor;


