import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import axios from 'axios';


class Settings extends Component{
    constructor(props){
        super(props);
        this.state ={
          settings: []
        }
        this.handleInputChange = this.handleInputChange.bind(this);
        this.url= localStorage.getItem("driverurl");
    }

    componentWillMount(){
      this.getSettings();
  }

  getSettings(){
       axios.get(`${this.url}/api/drivers/settings`)
      .then(response => {
          this.setState({settings: response.data}, () =>
      {
          console.log(this.state);
      })
      })
      .catch(err => console.log(err));
  }

  onSubmit(e){
    
    this.editSettings(this.state.settings);
    e.preventDefault();
}

editSettings(newSettings){
    axios.request({
        method:'put',
        url:`${this.url}/api/drivers/settings`,
        data: newSettings
    })         
    .then(response => {
        if (response.status==204){
            this.props.history.push(`/`);
        }
    })
    .catch(err => console.log(err));
}

handleInputChange(e){
    const target = e.target;
    const value = target.value;
    const name = target.name;

    let detailsCopy=this.state.settings;
    detailsCopy[name]=value;
    this.setState({settings: detailsCopy});
     console.log(this.state)
}
    
    render(){
return (
  <div>
    <h4>Settings</h4>
    <h6>Messaging configuration</h6>
    <form onSubmit={this.onSubmit.bind(this)}>
    <div className="input-field">
    <p>
      <label className="input-field">
        <input name="activation" ref="activation" class="filled-in" type="checkbox" checked={this.state.settings.activation} onChange={(e) => {
    this.handleInputChange({
      target: {
        name: e.target.name,
        value: e.target.checked,
      }
    });
  }}/>
        <span>Connect to a vf-OS pubSub</span>
      </label>
      </p>
    </div>                      
                    <div className="input-field">
                        <input type="text" name="broker" ref="broker" value={this.state.settings.broker} onChange={this.handleInputChange} />
                        <label className="active" htmlFor="broker">Broker url</label>
                        </div>
                        <div className="input-field">
                        <input type="text" name="userName" ref="userName" value={this.state.settings.userName} onChange={this.handleInputChange} />
                        <label className="active" htmlFor="userName">userName</label>
                        </div>
                        <div className="input-field">
                        <input type="text" name="platformDomain" ref="platformDomain" value={this.state.settings.platformDomain} onChange={this.handleInputChange} />
                        <label className="active" htmlFor="platformDomain">Domain of the Platform</label>
                        </div>
                        <div className="input-field">
                        <input type="text" name="routingKeys" ref="routingKeys" value={this.state.settings.routingKeys} onChange={this.handleInputChange} />
                        <label className="active" htmlFor="routingKeys">Routing Keys</label>
                        </div>
                    <input type="submit" value="Save" className="btn"/>
    </form>
  </div>
)
    }
  }

export default Settings;



