import React, {Component} from 'react';
import axios from 'axios';
import {Link} from 'react-router-dom';



class SensorDetails extends Component{
    constructor(props){
        super(props);
        this.state ={
            sensorData:[],
            sensorHistoric: {}
        }
        this.getHistoric = this.getHistoric.bind(this);
        this.url= localStorage.getItem("driverurl");
        
    }
    componentWillMount(){
        this.getData();
        // this.setSubscription();
        this.getHistoric();
        this.fakeSubscription();
    }

    getData(){
        let deviceId = this.props.match.params.did;
        let sensorId = this.props.match.params.sid;
       
       axios.get(`${this.url}/api/sensors/devices/${deviceId}/sensors/${sensorId}/Data`)
        .then(response => {
            let measure = response.data;
            measure.timestamp = measure.timestamp;
            this.setState({sensorData: response.data}, () =>
        {
              console.log(this.state);
        })
        })
        .catch(err => console.log(err));
    }

    fakeSubscription(){
        setInterval(this.getHistoric, 3000);
    }

    getHistoric(){
        let deviceId = this.props.match.params.did;
        let sensorId = this.props.match.params.sid;
        let end = encodeURIComponent(new Date());
        let start = new Date();
        start.getMinutes(start.getMinutes()-10);
        start = encodeURIComponent(start);
       
       axios.get(`${this.url}/api/sensors/devices/${deviceId}/sensors/${sensorId}/Data/HistoricData?start=${start}&end=${end}`)
        .then(response => {
            let measure = response.data[response.data.length-1];
            measure.timestamp = measure.timestamp;
            this.setState({sensorHistoric: measure}, () =>
        {
              // console.log(this.state);
        })
        })
        .catch(err => console.log(err));
    }

    setSubscription(){
        let deviceId = this.props.match.params.did;
        let sensorId = this.props.match.params.sid;
 
        axios.post(`${this.url}/api/sensors/devices/${deviceId}/sensors/${sensorId}/Subscribe`,{})
        .catch(err => console.log(err));
    }

    render(){
        return (
            <div>
            <div>
                <br />
                <Link className="btn grey" to={`/devices/${this.props.match.params.did}`}>Back</Link>


                <h5>Data from device: {this.props.match.params.did} and sensor: {this.props.match.params.sid}</h5>
                <ul className="collection">
                    <h4><li className="collection-item"><b>Value:</b> {this.state.sensorData.data} {this.state.sensorData.unit}</li></h4>
                    <li className="collection-item"><b>TimeStamp:</b> {this.state.sensorData.timestamp}</li>
                    <li className="collection-item"><b>Status:</b> {this.state.sensorData.status}</li>
                </ul>
            </div>
            
            </div>
        )
    }
}

export default SensorDetails;


