import React from 'react';
import { Switch, Route } from 'react-router-dom';

import Devices from './Devices';
import About from './About';
import Settings from './Settings';
import DeviceDetails from './DeviceDetails';
import AddDevice from './AddDevice';
import SensorDetails from './SensorDetails';
import EditDevice from './EditDevice';
import AddSensor from './AddSensor';
import EditSensor from './EditSensor';

const Main = () => (
    <main>
        <Switch>
            <Route exact path="/about" component={About}/>
            <Route exact path="/settings" component={Settings}/>    
            <Route exact path="/devices/add" component={AddDevice}/>
            <Route exact path="/devices/edit/:did" component={EditDevice}/>            
            <Route exact path="/devices/:id" component={DeviceDetails}/>
            <Route exact path="/devices/:did/sensors/add" component={AddSensor}/>
            <Route exact path="/devices/:did/sensors/:sid/edit" component={EditSensor}/>
            <Route exact path="/devices/:did/sensors/:sid" component={SensorDetails}/>
            <Route exact path="" component={Devices} />
        </Switch>
    </main>

)

export default Main;


