import React, {Component} from 'react';
import {Link} from 'react-router-dom';


class DeviceItem extends Component{
    constructor(props){
        super(props);
        this.state ={
            item:props.item
        }
        console.log(props.match);
    }
    render(){
        return (
          <tr>
            <td>{this.state.item._did}</td>
            <td><Link to={`/devices/${this.state.item._did}`}>{this.state.item.name}</Link></td>
          </tr>
        )
    }
}

export default DeviceItem;


