import React, {Component} from 'react';
import axios from 'axios';
import {Link} from 'react-router-dom';


class AddDevice extends Component{
    constructor(props){
        super(props);
        this.state ={
            proprietaryParameters:[]
        }
        this.url= localStorage.getItem("driverurl");
    }

    componentWillMount(){
        this.getDriverMetadata();
    }

    
    getDriverMetadata(){     
       
       axios.get(`${this.url}/api/drivers/metadata`)
        .then(response => {
            this.setState({proprietaryParameters: response.data.device_proprietaryParameters}, () =>
        {
            document.getElementsByClassName("progress")[0].remove();
            
        })
        })
        .catch(err => console.log(err));
    }

    onSubmit(e){
        const newDevice ={
            _did:  this.refs.did.value,
            name:  this.refs.name.value,
            needProcessing:  false,
            processingExpression:  "%v",
            propietaryParameters: [
                this.refs.propietaryparameters.value
            ]
        }
        this.addDevice(newDevice);
        e.preventDefault();
    }

    addDevice(newDevice){
        console.log(newDevice);
        axios.request({
            method:'post',
            url:`api/devices/devices/`,
            data: newDevice
        })         
        .then(response => {
            console.log(response);
        })
        .catch(err => console.log(err));
    }

    render(){
        const proprietaryParameters = this.state.proprietaryParameters.map((parameter, i) => {
            return (
                <div className="input-field">
                        <input type="text" name={parameter[0]} ref={parameter[0]} placeholder={parameter[1]} />
                        <label className="active" htmlFor={parameter[0]}>{parameter[0]}</label>
                        </div>
            )
        })

        return (
            <div>
                 <div class="progress">
                    <div class="indeterminate"></div>
                </div>
                <Link className="btn grey" to="/">Back</Link>
                <br />
                
                <h4>Register Device</h4>
                <form onSubmit={this.onSubmit.bind(this)}>
                    <div className="input-field">
                        <input type="text" name="did" ref="did" />
                        <label className="active" htmlFor="did">Device ID</label>
                        </div>
                        <div className="input-field">
                        <input type="text" name="name" ref="name" />
                        <label className="active" htmlFor="name">Name</label>
                        </div>
                        <div className="input-field">
                        <h6>Proprietary Parameters</h6>
                        {proprietaryParameters}
                    </div>
                    <input type="submit" value="Save" className="btn"/>
                </form>
            </div>
        )
    }
}

export default AddDevice;


