import React, {Component} from 'react';
import axios from 'axios';
import {Link} from 'react-router-dom';
import SensorItem from './SensorItem';


class DeviceDetails extends Component{
    constructor(props){
        super(props);
        this.state ={
            details:[],
            sensors:[]
        }
        this.url = localStorage.getItem("driverurl");
    }
    componentWillMount(){
        this.getDevice();
        this.getSensors();
    }

    getDevice(){
        let deviceId = this.props.match.params.id;
       
       axios.get(`${this.url}/api/devices/devices/${deviceId}`)
        .then(response => {
            this.setState({details: response.data[0]}, () =>
        {
            // console.log(this.state);
        })
        })
        .catch(err => console.log(err));
    }
    getSensors(){
        let deviceId = this.props.match.params.id;
       
       axios.get(`${this.url}/api/devices/devices/${deviceId}/sensors`)
            .then(response => {
                let sensorsCopy = response.data;
                for (let i=0; i< sensorsCopy.length;i++){
                    sensorsCopy[i].style = JSON.parse('{"color": "grey"}');
                }
                this.setState({sensors: sensorsCopy});
            })
            .catch(err => console.log(err));
    }

    onDelete(){
        let deviceId = this.state.details._did;
        
        axios.delete(`${this.url}/api/devices/devices/${deviceId}`)
            .then(response => {
                // pops OK or Cancel to proceed
                //var r = confirm(response);
                console.log(response);
                if (response.status==204){
                    this.props.history.push('/');
                }
            })
            .catch(err => console.log(err));
    }

    onCheckStatus(){
        let sensorsList = this.state.sensors;
            for (let i in sensorsList){
                let deviceId = sensorsList[i]._did;
                let sensorId = sensorsList[i]._sid;
               
       axios.get(`${this.url}/api/sensors/devices/${deviceId}/sensors/${sensorId}/Data`)
                .then(response => {
                    let sensorsCopy=this.state.sensors;
                    for (let j=0; j<sensorsCopy.length; j++){
                        if(sensorsCopy[j]._sid==response.data._sid){
                            if (response.data.status == "Good"){
                                sensorsCopy[j].style = JSON.parse('{"color": "green"}');
                            }else{
                                sensorsCopy[j].style = JSON.parse('{"color": "red"}');
                            }
                            break;
                        }
                    }
                    this.setState({sensors: sensorsCopy});
                })
                .catch(err => console.log(err));
            }
    }

    render(){
        const sensorItems = this.state.sensors.map((sensor, i) => {
            return (
                <SensorItem key={sensor._sid} item={sensor} />
            )
        })

        let deviceDetails;
        if (this.state.details.propietaryParameters){
            deviceDetails = this.state.details.propietaryParameters.map((pp, i) => {
                return (
                    <li className="collection-item"><b>{pp.name}:</b>{pp.value}</li>
                )
            })
        }
        

        return (
            <div>
                <br />
                <Link className="btn grey" to="/">Back</Link>
                
                <button className="btn red right" onClick={this.onDelete.bind(this)}>Delete</button>
                <Link className="btn right" to={`/devices/edit/${this.state.details._did}`}>Edit</Link>
                

                <h6>{this.state.details.name}</h6>
                <ul className="collection">
                            <li className="collection-item"><b>Name:</b> {this.state.details.name}</li>
                            <li className="collection-item"><b>Need processing:</b> {this.state.details.needProcessing?"true":"false"}</li>
                            <li className="collection-item"><b>Computing Expression:</b> {this.state.details.processingExpression}</li>
                </ul>
                <h6>Propietary Parameters</h6>
                <ul className="collection">
                    {deviceDetails}
                </ul>
                
                
                <div>

                    <h4>Sensors<button className="btn grey right" onClick={this.onCheckStatus.bind(this)}>Check Status</button><Link className="btn blue right" to={`/devices/${this.state.details._did}/sensors/add`}>Add Sensor</Link></h4>
                    <table className="striped">
                    <thead>
                        <tr>
                            <th>Id</th>
                            <th>Name</th>
                            <th>Protocol</th>
                            <th>Type</th>
                            <th>Unit</th>
                            <th>Status</th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                        {sensorItems}
                        </tbody>
                        </table>
                
                </div>
            </div>
        )
    }
}

export default DeviceDetails;


