import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import axios from 'axios';


class SensorItem extends Component{
    constructor(props){
        super(props);
        this.state ={
            item:props.item
        }
        this.url= localStorage.getItem("driverurl");
    }

    onDelete(){
        let deviceId = this.state.item._did;
        let sensorId = this.state.item._sid;
        
        axios.delete(`${this.url}/api/sensors/devices/${deviceId}/sensors/${sensorId}`)
            .then(response => {
                // pops OK or Cancel to proceed
                //var r = confirm(response);
                console.log(response);
                if (response.status==204){
                    this.props.history.push('/');
                }
            })
            .catch(err => console.log(err));
    }
    
    render(){

        return (
            <tr>
            <td>{this.state.item._sid}</td>
            <td><Link to={`/devices/${this.state.item._did}/sensors/${this.state.item._sid}`}>{this.state.item.name}</Link></td>
            <td>{this.state.item.driver.protocol}</td>
            <td>{this.state.item.properties.name}</td>
            <td>{this.state.item.properties.unit}</td>
            <td><label id={this.state.item._sid} className="fas fa-signal" style={this.state.item.style}></label></td>
            <td>
                <button className="btn red right" onClick={this.onDelete.bind(this)} value={this.state.item._sid}>Delete</button>
                <Link className="btn right" to={`/devices/${this.state.item._did}/sensors/${this.state.item._sid}/edit`}>Edit</Link>
            </td>
                
          </tr>
        )
    }
}

export default SensorItem;


