import React, {Component} from 'react';
import axios from 'axios';
import {Link} from 'react-router-dom';


class EditDevice extends Component{
    constructor(props){
        super(props);
        this.state ={
            details:[]
        }
        this.handleInputChange = this.handleInputChange.bind(this);
        this.handleInputChange2 = this.handleInputChange2.bind(this);
        this.url= localStorage.getItem("driverurl");
    }

    componentWillMount(){
        this.getDevice();
    }

    getDevice(){
        let deviceId = this.props.match.params.did;

       axios.get(`${this.url}/api/devices/devices/${deviceId}`)
        .then(response => {
            this.setState({details: response.data[0]}, () =>
        {
            console.log(this.state);
        })
        })
        .catch(err => console.log(err));
    }

    onSubmit(e){
        let newDevice = this.state.details;      
        this.editDevice(newDevice);
        e.preventDefault();
    }

    editDevice(newDevice){
        axios.request({
            method:'put',
            url:`${this.url}/api/devices/devices/${newDevice._did}`,
            data: newDevice
        })         
        .then(response => {
            if (response.status==204){
                this.props.history.push(`/devices/${newDevice._did}`);
            }
        })
        .catch(err => console.log(err));
    }

    handleInputChange(e){
        const target = e.target;
        const value = target.value;
        const name = target.name;

        let detailsCopy=this.state.details;
        detailsCopy[name]=value;
        this.setState({details: detailsCopy});
         console.log(this.state)
    }
    handleInputChange2(e){
        const target = e.target;
        const value = target.value;
        const name = target.name;
        

        let ppCopy= this.state.details;

        for (let i=0; i<ppCopy.propietaryParameters.length; i++){
            if(ppCopy.propietaryParameters[i].name==name){ppCopy.propietaryParameters[i].value=value; break;}
        }

        this.setState({details: ppCopy});
         console.log(this.state)
    }

    render(){
        let proprietaryParameters;
        if(typeof this.state.details.propietaryParameters !== "undefined"){
            proprietaryParameters = this.state.details.propietaryParameters.map((parameter, i) => {
                return (
                    <div className="input-field">
                            <input type="text" name={parameter.name} ref={parameter.name} value={parameter.value} onChange={this.handleInputChange2}/>
                            <label className="active" htmlFor={parameter.name}>{parameter.name}</label>
                            </div>
                )
            })
        }
         
        return (
            <div>
                <Link className="btn grey" to={`/devices/${this.props.match.params.did}`}>Back</Link>
                <br />
                <h4>Edit Device</h4>
                <form onSubmit={this.onSubmit.bind(this)}>
                    <div className="input-field">
                        <input type="text" name="did" ref="did" value={this.state.details._did} />
                        <label className="active" htmlFor="did">Device ID</label>
                        </div>
                        <div className="input-field">
                        <input type="text" name="name" ref="name" value={this.state.details.name} onChange={this.handleInputChange} />
                        <label className="active" htmlFor="name">Name</label>
                        </div>
                        <div className="input-field">
                        <h6>Proprietary Parameters</h6>
                        {proprietaryParameters}
                    </div>
                    <input type="submit" value="Save" className="btn"/>
                </form>
            </div>
        )
    }
}

export default EditDevice;


