import React, {Component} from 'react';
import axios from 'axios';
import {Link} from 'react-router-dom';
import DeviceItem from './DeviceItem';


class Devices extends Component{
    constructor(){
        super();
        this.state = {
            devices: []
        }
        this.url="";
        if (window.location.pathname.split('/')[1].length > 0){
            this.url="/"+ window.location.pathname.split('/')[1];
        };
        if (!localStorage.getItem("urlprocessed")){
            localStorage.setItem("driverurl",this.url);
            localStorage.setItem("urlprocessed",true);
        }else{
            this.url= localStorage.getItem("driverurl");
        };

    }

    componentWillMount(){
        this.getDevices();
    }

    getDevices(){
       axios.get(`${this.url}/api/devices/devices`)
        .then(response => {
            this.setState({devices: response.data}, () =>
        {
            console.log(this.state);
        })
        })
        .catch(err => console.log(err));
    }

    render(){
        const devicesItems = this.state.devices.map((device, i) => {
            return (
                <DeviceItem key={device._did} item={device} />
            )
        })

        var divStyle = {
                position: 'absolute',
                bottom: 0,
                width: "100%"
        };

        return (
            <div>
                <h4>List of Devices</h4>
                <Link to="/devices/add" className="btn blue right">Add Device</Link>
                <table className="striped">
                <thead>
                    <tr>
                        <th>Id</th>
                        <th>Name</th>
                    </tr>
                </thead>
                <tbody>
                    {devicesItems}
                    </tbody>
                    </table>
                    <div style={divStyle}>
                        <label>UI v0.9.8</label><br />
                        <label>changelog:</label><br />
                        <label>  - changes routes to work on the platform </label><br />
                    </div>
            </div>
        )
    }
}

export default Devices;


