import React, {Component} from 'react';
import axios from 'axios';
import {Link} from 'react-router-dom';
import SensorDetails from './SensorDetails';


class EditSensor extends Component{
    constructor(props){
        super(props);
        this.state ={
            proprietaryParameters:[],
            sensor:[],
            sensorObject: {}
        }
        this.handleInputChange = this.handleInputChange.bind(this);
        this.handleInputChange2 = this.handleInputChange2.bind(this);
        this.handleInputChange3 = this.handleInputChange3.bind(this);
        this.handleInputChange4 = this.handleInputChange4.bind(this);
        this.handleInputChange5 = this.handleInputChange5.bind(this);
        this.url= localStorage.getItem("driverurl");
    }

    componentWillMount(){
        // this.getDriverMetadata();
        this.getSensor();
    }

    
    getDriverMetadata(){
       
       axios.get(`${this.url}/api/metadata/getMetadata`)
        .then(response => {
            this.setState({proprietaryParameters: response.data.sensor_proprietaryParameters}, () =>
        {
            console.log(this.state);
        })
        })
        .catch(err => console.log(err));
    }

    getSensor(){
        let deviceId = this.props.match.params.did;
        let sensorId = this.props.match.params.sid;

       axios.get(`${this.url}/api/sensors/devices/${deviceId}/sensors/${sensorId}`)
        .then(response => {            
             this.setState({sensorObject: response.data[0], sensor: Object.keys(response.data[0]).map(key => [key, typeof response.data[0][key] === 'object'? JSON.stringify(response.data[0][key]) : response.data[0][key]])}, () =>
        {
            console.log(this.state);
        })
        })
        .catch(err => console.log(err));
    }


    onSubmit(e){
        // const newSensor ={
        //     _did:  this.props.match.params.did,
        //     _sid:  this.refs.sid.value,
        //     name:  this.refs.name.value,
        //     needProcessing:  true,
        //     processingExpression:  this.refs.processingExpression.value,
        //     propietaryParameters: [
        //         this.refs.propietaryparameters.value
        //     ]
        // }
        //this.addDevice(newSensor);
        this.editDevice(this.state.sensorObject);
        e.preventDefault();
    }

    editDevice(newSensor){
        console.log(newSensor);
        
        axios.request({
            method:'put',
            url:`${this.url}/api/sensors/devices/${newSensor._did}/sensors/${newSensor._did}`,
            data: newSensor
        })         
        .then(response => {
            if (response.status==204){
                this.props.history.push(`/devices/${newSensor._did}`);
            }
        })
        .catch(err => console.log(err));
    }

    handleInputChange(e){
        const target = e.target;
        const value = target.value;
        const name = target.name;

        let detailsCopy=this.state.sensorObject;
        detailsCopy[name]=value;
        this.setState({sensorObject: detailsCopy});
         

        // const target = e.target;
        // const value = target.value;
        // const name = target.name;
        

        // let sensorCopy= this.state.sensorObject;

        // sensorCopy[name]=value;

        // this.setState({sensorObject: sensorCopy, sensor: Object.keys(sensorCopy).map(key => [key, typeof sensorCopy[key] === 'object'? JSON.stringify(sensorCopy[key]) : sensorCopy[key]])});
        // console.log(this.state)
    }

    handleInputChange2(e){
        const target = e.target;
        const value = target.value;
        const name = target.name;
        

        let ppCopy= this.state.sensorObject;

        for (let i=0; i<ppCopy.propietaryParameters.length; i++){
            if(ppCopy.propietaryParameters[i].name==name){ppCopy.propietaryParameters[i].value=value; break;}
        }

        this.setState({sensorObject: ppCopy});
    }
    handleInputChange3(e){
        const target = e.target;
        const value = target.value;
        const name = target.name;
        

        let detailsCopy=this.state.sensorObject;
        detailsCopy.events[0][name]=value;
        this.setState({sensorObject: detailsCopy});
    }

    handleInputChange4(e){
            const target = e.target;
            const value = target.value;
            const name = target.name;
    
            let detailsCopy=this.state.sensorObject;
            detailsCopy.driver[name]=value;
            this.setState({sensorObject: detailsCopy});
    }

    handleInputChange5(e){
        const target = e.target;
            const value = target.value;
            const name = target.name;
    
            let detailsCopy=this.state.sensorObject;
            detailsCopy.properties[name]=value;
            this.setState({sensorObject: detailsCopy});
    }
    

    render(){
        
        const sensorDetails = this.state.sensor.map((pair, i) => {
      
            if (!pair[0].includes("id")){
                return (
                    <div className="input-field">
                            <input type="text" name={pair[0]} ref={pair[0]} value={pair[1]} onChange={this.handleInputChange}/>
                            <label className="active" htmlFor={pair[0]}>{pair[0]}</label>
                            </div>
                )
            }
        }, this);

        let properties=[];
        if(typeof this.state.sensorObject.properties !== "undefined"){
            for (let item in this.state.sensorObject.properties){
                properties.push(
                    <div className="input-field">
                            <input type="text" name={item} ref={item} value={this.state.sensorObject.properties[item]} onChange={this.handleInputChange5}/>
                            <label className="active" htmlFor={item}>{item}</label>
                    </div>
                );
            }
        }

        let driver=[];
        if(typeof this.state.sensorObject.driver !== "undefined"){
            for (let i in this.state.sensorObject.driver){
                driver.push(
                    <div className="input-field" >
                            <input type="text" name={i} ref={i} value={this.state.sensorObject.driver[i]} onChange={this.handleInputChange4} readonly="true" disabled/>
                            <label className="active" htmlFor={i}>{i}</label>
                    </div>
                );
            }
        }


        let trigger=[];
        if(typeof this.state.sensorObject.events !== "undefined"){
                trigger.push(
                    <div className="input-field">
                            <input type="text" name="type" ref="type" value={this.state.sensorObject.events[0].type} onChange={this.handleInputChange3}/>
                            <label className="active" htmlFor="type">Type</label>
                    </div>
                );
                
                    trigger.push(
                        <div className="input-field">
                                <input type="text" name="intervalTime" ref="intervalTime" value={this.state.sensorObject.events[0].intervalTime} onChange={this.handleInputChange3}/>
                                <label className="active" htmlFor="intervalTime">Sampling Interval</label>
                        </div>
                    );
                }

        let proprietaryParameters;
        if(typeof this.state.sensorObject.propietaryParameters !== "undefined"){
            proprietaryParameters = this.state.sensorObject.propietaryParameters.map((parameter, i) => {
                return (
                    <div className="input-field">
                            <input type="text" name={parameter.name} ref={parameter.name} value={parameter.value} onChange={this.handleInputChange2}/>
                            <label className="active" htmlFor={parameter.name}>{parameter.name}</label>
                            </div>
                )
            })
        }

        
        return (
            <div>
                <Link className="btn grey" to={`/devices/${this.props.match.params.did}`}>Back</Link>
                <br />
                
                <h4>Register Sensor</h4>
                <form onSubmit={this.onSubmit.bind(this)}>
                <div className="input-field">
                    <input type="text" name="name" ref="name" value={this.state.sensorObject.name} onChange={this.handleInputChange}/>
                    <label className="active" htmlFor="name">Name</label>
                </div>
                <h5>Properties</h5>
                {properties}
                <h5>Driver</h5>
                {driver}
                <h5>Events</h5>
                {trigger}
                <h5>PropietaryParameters</h5>
                {proprietaryParameters}
                <input type="submit" value="Save" className="btn"/>
                </form>
            </div>
        )
    }
}

export default EditSensor;


